.header-color {
  background: #833ab4;  
  background: -webkit-linear-gradient(to right, #fcb045, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, #fcb045, #fd1d1d, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}

.landing-grid {
  background: #833ab4;  
  background: -webkit-linear-gradient(to right, blue, #fd1d1d, #833ab4);  /* Chrome 10-25, Safari 5.1-6 */
  background: linear-gradient(to right, rgb(103, 204, 238), #98f798, #833ab4); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
  text-align: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 86%;
}

.projects-grid {
  display: flex;
}

/* Resume Page css */
.resume-right-col {
  background: #27221F;
  color: white;
  padding: 2em;
}

/* contact page css */

.contact-body {
  margin: auto;
  /* position: fixed; */
  width: 100%;
  height: 100%;
  justify-content: center;

}

.contact-grid {
  text-align: center;
  justify-content: center;
  width: 80%;
  height: 450px;
  background: white;
  color: black;
 
}

.contact-grid h2 {
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 66px;
  padding-right: 5rem;
}


/* contact page css */

.avatar-img {
  height: 250px;
  padding-top: 5em;
}

.banner-text {
  background-color: black;
  opacity: .8;
  width: 75%;
  margin: auto;
  border-radius: 10px;
}

.banner-text h1 {
  font-size: 66px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 5px dotted white;
  width: 50%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  display: flex;
  justify-content: space-between;
  width: 50%;
  margin: auto;
}


.social-links i {
  color: white;
  font-size: 5em;
  padding: 8px;
}

.site-link{
  margin: 0;
}
/* for mobile screens */
@media only screen and (max-width: 600px) {
  .social-links{
 margin: 34px;
} 

.welcome{
 width: 350px; 
}

.banner-text h1 {
 font-size: 54px;
}

.social-links i {
   font-size: 4em !important;
   padding-right: 8px !important;
   padding-bottom: 8px !important;
   padding-top: 0 !important;
   padding-left: 0!important;
}


.landing-grid {
max-height: 78% !important;
overflow-y: scroll;
}

}

@media (min-width: 480px) and (max-width: 839px){
.mdl-cell--6-col, .mdl-cell--6-col-tablet.mdl-cell--6-col-tablet {
    width: 100%;
}
}


@media (max-width: 479px)
{
.mdl-cell {

  width:100% !important
}

.contact-list i {

  padding-right: 2rem !important;
}
}

@media (max-width: 1026px){
  .projects-grid{
    flex-direction: column;
  }
  .mdl-card{
    margin-bottom: 10px !important;
  }
}